export default {
  data () {
    return {
      goods: {},
      activeIndex: 0,
      tabList: [
        '图文介绍',
        '规格参数'
      ]
    }
  },
  onLoad (options) {
    let goodsId = options.goodsId
    // 根据id查询商品的详情
    this.queryGoods(goodsId)
  },
  // 分享
  onShareAppMessage () {
    return {
      title: this.goods.goods_name,
      imageUrl: this.goods.pics[0].pics_big
    }
  },
  methods: {
    // 跳转到支付页
    toBuy () {
      wx.navigateTo({ url: '/pages/pay/main?goodsId=' + this.goods.goods_id })
    },
    // 加入购物车
    add2Cart () {
      this.$store.commit('add2Cart', this.goods.goods_id)
    },
    // 跳转购物车
    toCart () {
      wx.switchTab({ url: '/pages/cart/main' })
    },
    // 预览图片
    prevImg (index) {
      let urls = []
      this.goods.pics.forEach(v => {
        urls.push(v.pics_big)
      })
      wx.previewImage({
        current: urls[index],
        urls: urls // 需要预览的图片http链接列表
      })
    },
    async queryGoods (goodsId) {
      this.goods = await this.$request({
        url: '/api/public/v1/goods/detail',
        data: {
          goods_id: goodsId
        }
      })
    }
  }
}
